.ditty-field {
  padding: 15px 10px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  &__label {
    display: block;
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  &__help-icon {
    color: #93999f;
  }
  &__help {
    font-size: 13px;
    line-height: 16px;
    margin: 7px 0 10px;
  }
  &--help {
    .ditty-field__help-icon {
      color: $green;
    }
  }
  &__input {
    flex: 1;
    &__container {
      display: flex;
      align-items: center;
      gap: 8px;
    }
  }
  .components-base-control__field {
    margin: 0;
  }
  input:not([type="button"]):not([type="checkbox"]):not([type="radio"]),
  select,
  textarea {
    width: 100%;
    min-height: 0;
    font-size: 14px;
    line-height: 24px;
    border: 1px solid #dfdfdf;
    border-radius: 3px;
    padding: 5px 10px;
    margin: 0;
  }
}

.ditty-field__input--radio {
  div[role="radiogroup"] {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  &.ditty-field__input--inline {
    div[role="radiogroup"] {
      flex-direction: row;
      gap: 15px;
    }
  }
}

.ditty-field__input--unit {
  display: flex;
  align-items: stretch;
  gap: 5px;
  select {
    flex: 0 0 auto;
    width: 40px;
    font-size: 12px;
    text-align: center;
    padding-left: 5px;
    padding-right: 5px;
    background: transparent;
  }
}

.ditty-field__input--slider {
  .components-range-control__track {
    color: $green;
    + span span {
      background-color: $green;
      &:before {
        background-color: transparentize($green, 0.5);
      }
    }
  }
}

.ditty-field__input--color {
  position: relative;
  display: flex;
  align-items: stretch;
  gap: 5px;
  &__swatch {
    flex: 0 0 auto;
    width: 36px;
    height: 36px;
    border: 1px solid #dfdfdf;
    border-radius: 3px;
  }
  &__popover {
    position: absolute;
    z-index: 9;
    top: calc(100% + 5px);
  }
}

$spacing_color: #ccc;
$spacing_highlight_color: $dark_gray;
.ditty-field__input--spacing,
.ditty-field__input--radius {
  display: flex;
  gap: 5px;
  &__box {
    flex: 0 0 auto;
    position: relative;
    width: 20px;
    height: 20px;
    span {
      position: absolute;
      display: block;
      background: $spacing_color;
      &.active {
        background-color: $dark_gray;
      }
    }
  }
  &__inputs {
    display: flex;
    gap: 5px;
    flex-wrap: wrap;
  }
  &__input {
    width: calc(50% - 5px);
  }
  .ditty-field--unit {
    padding: 0;
    border: none;
  }
  .ditty-field__input--unit {
    gap: 0;
    input {
      border-top-right-radius: 0 !important;
      border-bottom-right-radius: 0 !important;
      border-right: none !important;
    }
    select {
      border-top-left-radius: 0 !important;
      border-bottom-left-radius: 0 !important;
    }
  }
}

.ditty-field__input--spacing {
  &__box {
    span {
      &:nth-child(1) {
        top: 0;
        left: 3px;
        width: calc(100% - 6px);
        height: 2px;
      }
      &:nth-child(2) {
        bottom: 0;
        left: 3px;
        width: calc(100% - 6px);
        height: 2px;
      }
      &:nth-child(3) {
        top: 3px;
        left: 0;
        height: calc(100% - 6px);
        width: 2px;
      }
      &:nth-child(4) {
        top: 3px;
        right: 0;
        height: calc(100% - 6px);
        width: 2px;
      }
    }
  }
}

$radius: 9px;
.ditty-field__input--radius {
  &__box {
    border-radius: 5px;
    overflow: hidden;
    &:before {
      position: absolute;
      top: 2px;
      left: 2px;
      content: "";
      width: 16px;
      height: 16px;
      background: #fff;
      z-index: 9;
      transform: rotate(45deg);
    }
    &:after {
      position: absolute;
      top: 2px;
      left: 2px;
      content: "";
      width: 16px;
      height: 16px;
      background: #fff;
      border-radius: 3px;
      z-index: 9;
    }
    span {
      width: 10px;
      height: 10px;

      &:nth-child(1) {
        top: 0;
        left: 0;
      }
      &:nth-child(2) {
        top: 0;
        right: 0;
      }
      &:nth-child(3) {
        bottom: 0;
        left: 0;
      }
      &:nth-child(4) {
        bottom: 0;
        right: 0;
      }
    }
  }
}
